* {
  padding: 0;
  margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.encabezado {
  display: flex;
  background-color: black;
  color: white;
  justify-content: space-between;
  align-items: center;
}

.encabezado div {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.section {
  display: flex;
  flex-flow: column nowrap;
  padding: 0.2em;
}

.section h1,
p {
  padding-top: 0.2em;
}

.footer {
  display: flex;
  width: 100%;
  height: 10vh;
  background-color: black;
  color: white;
  bottom: 0;
  position: fixed;
  justify-content: center;
  align-items: flex-start;
}

.tablaPrincipal {
  display: flex;
  flex-flow: column nowrap;
  padding: 0.1em;
  border: 5px solid red;
}

.tablaPrincipal tr {
  display: flex;
  justify-content: space-between;
  padding: 0.1em;
}

.tablaPrincipal .tablacabecera {
  border: 5px solid rgb(75, 172, 236);
}

.tablaPrincipal .tablacuerpo {
  border: 5px solid green;
  margin-top: 0.1em;
}

.tablaPrincipal .tablacuerpo .boton tr td button {
  border-radius: 0.2em;
  background-color: blue;
  border: 5px solid transparent;
  color: white;
  font-size: 20px;
  padding: 0.3em;
}

.tablaPrincipal .tablacuerpo .boton tr td button:hover {
  border: 5px solid orange;
}